ng: 5px;\r\n    }\r\n  }\r\n}\r\n","/*\r\n  Ordered & Un-ordered Column List\r\n*/\r\nul,\r\nol{\r\n  &.two-column-list{\r\n    columns: 2;\r\n    column-gap: 40px;\r\n  }\r\n  &.three-column-list{\r\n    columns: 3;\r\n    column-gap: 40px;\r\n  }\r\n  &.four-column-list{\r\n    columns: 4;\r\n    column-gap: 40px;\r\n  }\r\n  &.five-column-list{\r\n    columns: 5;\r\n    column-gap: 40px;\r\n  }\r\n}\r\nol{\r\n  li{\r\n    margin-bottom: 20px;\r\n    &::marker{\r\n      font-weight: 700;\r\n      line-height: 1;\r\n      font-size: 1.2em;\r\n    }\r\n  }\r\n}\r\n@media handheld, only screen and (max-width: map-get(getThemeProperty(breakpointResponsive), phone)) {\r\n  ul,\r\n  ol{\r\n    &.two-column-list,\r\n    &.three-column-list,\r\n    &.four-column-list,\r\n    &.five-column-list{\r\n      columns: 1;\r\n    }\r\n  }\r\n}\r\n","@import 'font-size-mixin';\r\n\r\n/*\r\n\tTYPOGRAPHY\r\n\tDefine all basic html tags. These can be overridden in but this should be the base.\r\n*/\r\nbody {\r\n\tfont: 400 1rem/1rem getThemeProperty(fontText);\r\n}\r\n/* H1 */\r\n$h1-font-sizes: (\r\n  desktop: (2.5em, 1),\r\n  tablet: (2.5em, 1),\r\n  phone: (2.5em, 1)\r\n);\r\nh1{\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontHeader);\r\n  font-weight: 700;\r\n\t@include font-size($h1-font-sizes);\r\n}\r\n/* H2 */\r\n$h2-font-sizes: (\r\n  desktop: (2.250em, 1),\r\n  tablet: (2.250em, 1),\r\n  phone: (2.250em, 1)\r\n);\r\nh2{\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontHeader);\r\n  font-weight: 700;\r\n\t@include font-size($h2-font-sizes);\r\n}\r\n/* H3 */\r\n$h3-font-sizes: (\r\n  desktop: (1.500em, 1),\r\n  tablet: (1.500em, 1),\r\n  phone: (1.500em, 1)\r\n);\r\nh3{\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontHeader);\r\n  font-weight: 700;\r\n\t@include font-size($h3-font-sizes);\r\n}\r\n/* H4 */\r\n$h4-font-sizes: (\r\n  desktop: (1.313em, 1),\r\n  tablet: (1.3